'use client'

import { useState } from 'react'
import Image from "next/image";

export default function Home() {
  const [youtubeUrl, setYoutubeUrl] = useState('')
  const [isActive, setIsActive] = useState(false)
  const [activeTab, setActiveTab] = useState('Quick Summary')

  const TABS = ['Quick Summary', 'Key Takeaways', 'Interactive Chat']

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    // TODO: Add API call to backend here
    console.log('Submitted URL:', youtubeUrl)
    setIsActive(true)
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-white to-gray-50 dark:from-gray-900 dark:to-gray-800">
      <div className="max-w-4xl mx-auto px-4 py-16 sm:px-6 sm:py-24 lg:px-8">
        <div className="text-center">
          {/* Main Title */}
          <h1 className="text-6xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-purple-600 to-pink-600 mb-4">
            TL;DL
          </h1>
          
          {/* Subtitle */}
          <h2 className="text-4xl font-semibold text-gray-900 dark:text-gray-100 mb-8">
            Don&apos;t Pod Around
          </h2>
          
          {/* Description */}
          <p className="text-lg text-gray-600 dark:text-gray-300 mb-12">
            Get instant summaries and chat with your favorite podcasts
          </p>

          {/* URL Input Form */}
          <form onSubmit={handleSubmit} className="max-w-2xl mx-auto">
            <div className="flex flex-col sm:flex-row gap-4">
              <input
                type="url"
                placeholder="Paste your YouTube podcast link here..."
                value={youtubeUrl}
                onChange={(e) => setYoutubeUrl(e.target.value)}
                className="flex-1 px-6 py-4 rounded-full border border-gray-200 dark:border-gray-700 
                          bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100
                          shadow-sm focus:ring-2 focus:ring-purple-500 focus:border-transparent
                          transition-all duration-200 ease-in-out"
              />
              <button
                type="submit"
                className="px-8 py-4 rounded-full bg-gradient-to-r from-purple-600 to-pink-600
                         text-white font-semibold shadow-lg hover:shadow-xl
                         transform hover:-translate-y-0.5 transition-all duration-200 ease-in-out"
              >
                Summarize
              </button>
            </div>
          </form>

          {/* Features Tab Bar */}
          <div className="mt-16 flex justify-center">
            <div className="p-[1px] rounded-lg bg-gradient-to-r from-purple-600 to-pink-600">
              <div className="flex space-x-4 bg-white dark:bg-gray-900 rounded-lg p-1">
                {TABS.map((tab) => (
                  <button
                    key={tab}
                    disabled={!isActive}
                    onClick={() => isActive && setActiveTab(tab)}
                    className={`px-6 py-2 text-lg font-medium rounded-lg
                             border transition-all duration-200 ease-in-out
                             ${!isActive 
                               ? 'border-gray-200 dark:border-gray-700 text-gray-500 opacity-50 cursor-not-allowed' 
                               : activeTab === tab
                                 ? 'border-purple-600 dark:border-purple-500 text-purple-600 dark:text-purple-500 bg-purple-50 dark:bg-purple-900/20'
                                 : 'border-gray-200 dark:border-gray-700 text-gray-900 dark:text-gray-100 hover:border-purple-600 dark:hover:border-purple-500 hover:text-purple-600 dark:hover:text-purple-500'}`}
                  >
                    {tab}
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
